
@import '../../common/css/font/方正中粗雅宋_GBK.css';
.content {
  margin-left: 0.5rem;
}
.content .c-title {
  border-bottom: 0.03125rem solid #EFEFEF;
}
.content .c-title .title {
  display: inline-block;
  font-family: "方正中粗雅宋_GBK";
  font-size: 0.5rem;
  font-weight: 500;
  line-height: 1.25rem;
}
.content .c-title .amount {
  display: inline-block;
  color: #848B9D;
  font-size: 0.40625rem;
  margin-left: 0.28125rem;
}
.content .c-title .more {
  display: inline-block;
  float: right;
  background: url("arrow-right.png") no-repeat center center;
  background-size: 0.3125rem 0.40625rem;
  width: 1.25rem;
  height: 1.25rem;
}
.content .c-book-name {
  padding-bottom: 0.5rem;
  /*padding-right: 0.5rem;*/
}
.content .c-book-name span {
  display: inline-block;
  line-height: 0.90625rem;
  width: 2.3125rem;
  margin-top: 0.375rem;
  margin-right: 0.625rem;
  text-align: center;
  color: #848B9D;
  border: 0.03125rem solid #848B9D;
  border-radius: 1.5625rem;
}
.content .c-book-name span:last-child {
  margin-right: 0;
}



