
.male .container {
  margin: 0.28125rem 24px 0;
  width: auto;
}
.male .h-carousel {
  margin-top: 0.3125rem;
}
.male .h-carousel span.swiper-pagination-bullet-active {
  background-color: #E92737;
}
.male .h-carousel span.swiper-pagination-bullet {
  width: 0.15625rem;
  height: 0.15625rem;
}
.male .h-search a {
  display: block;
  width: 100%;
  height: 1.0rem;
  margin: 0.21875rem 0;
  border-radius: 0.125rem;
  line-height: 1.0rem;
  background: #fff;
  text-align: center;
}
a, a:link, a:visited, a:hover, a:active {
  color: #8F939B;
}
.male .h-search a span:nth-child(1) {
  display: inline-block;
  width: 0.921875rem;
  height: 0.921875rem;
  vertical-align: middle;
  background: url("../../components/header/header-search.png") no-repeat;
  background-size: 0.921875rem 0.921875rem;
}
.male .h-search a span:nth-child(2) {
  padding-top: 0.078125rem;
}
.male .h-navigation {
  box-sizing: border-box;
  width: 100%;
  height: 2.625rem;
  background-color: #fff;
  padding: 0.5rem 0.3125rem;
}
.male .h-navigation .nav-single {
  width: 1.40625rem;
  height: 1.59375rem;
  text-align: center;
  float: left;
  margin: 0 0.234375rem;
}
.male .h-navigation .nav-single img {
  display: inline-block;
  margin-left: 0.046875rem;
  width: 0.9375rem;
  height: 0.9375rem;
}
.male .h-navigation .nav-single h2 {
  margin-top: 0.046875rem;
  font-weight: bold;
}
/* end of header */
div.insert {
  width: 100%;
  height: 1.90625rem;
  text-align: center;
}
div.insert a {
  display: inline-block;
  width: 4.25rem;
  height: 0.90625rem;
  margin-top: 0.5rem;
  line-height: 0.90625rem;
  border: 0.03125rem solid #ed424b;
  border-radius: 1.5625rem;
  color: #ed424b;
  text-align: center;
}
div.insert a.new-book {
  margin-right: 0.5625rem;
}



